
ul{
    margin: 0;
    padding: 0;
}

li{
    list-style: none;
}
/* a{
    text-decoration: none;
} */

.header{
    border-bottom: 1px solid #e2e8f0;
}

.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 4rem;
    padding-left: 4rem;
    padding-right: 4rem;
}

 .hamb{
    display: none;
}

.bar{
    display: block;
    height: 3px;
    width: 25px;
    margin: 5px auto;
    transition: 0.3s;
    background-color: #101010;
}

.nav_menu{
    display: flex;
    justify-content: space-between;
    align-items: center;

}
.nav_link{
    font-size: 1.6rem;
    font-weight: 400;
    color: #475569;
   margin-right: 3rem;
}
.nav_link:hover{
    color:#482ff7;
}
/*  .nav_link::last-child{
    margin-right: 40rem;
} */

.nav_logo{
    font-size: 2.1rem;
    font-weight: 600;
    color: #482ff7;

}

@media only screen and (max-width: 768px){
    .nav_menu{
        position: fixed;
        flex-direction: column;
        background-color: #fff;
        width: 100%;
        text-align: center;;
        transition: 0.3s;
        box-shadow: 0 10px 27px rgba(0,0,0,0.5);
        left: -100%;
        top: 4rem;
    }
.nav_menu-active{
    left:0;
}
.nav_item{
    margin: 4rem 0;

}

.hamb{
    display: block;
    cursor: pointer;
}
.hamb-active .bar::nth-child(2){
    opasity: 0
}
.hamb-active .bar::nth-child(1){
    transform: translateY(8p) rotate(45deg);
}
.hamb-active .bar::nth-child(3){
    transform: translateY(-8px) rotate(-l45deg);
}
}